<template>
    <div class='ziliao'>
        <div class="title">{{$t('bianjiziliao[0]')}}</div>
        <div class="hr"></div>
        <div class="item">
            <div>{{$t('bianjiziliao[1]')}}：</div>
            <div>
                <el-input v-model="form.name" :placeholder="$t('bianjiziliao[2]')"></el-input>
            </div>
            <div class="zhanwei"></div>
        </div>
        <div class="item">
            <div>{{$t('bianjiziliao[3]')}}：</div>
            <div>
                <el-input v-model="form.real_name" :placeholder="$t('bianjiziliao[4]')"></el-input>
            </div>
             <div class="zhanwei"></div>
        </div>
        <div class="item">
            <div>{{$t('bianjiziliao[5]')}}：</div>
            <div>
                <el-radio-group v-model="form.sex">
                    <el-radio :label="$t('bianjiziliao[6]')" :value='1'></el-radio>
                    <el-radio :label="$t('bianjiziliao[7]')" :value='2'></el-radio>
                </el-radio-group>
            </div>
             <div class="zhanwei"></div>
        </div>
        <div class="item">
            <div>{{$t('bianjiziliao[8]')}}：</div>
            <div>
                <el-input v-model="form.phone" :placeholder="$t('bianjiziliao[9]')"></el-input>
            </div>
             <div class="zhanwei"></div>
        </div>
        <div class="item">
            <div>{{$t('bianjiziliao[10]')}}：</div>
            <div>
                <el-input v-model="form.email" :placeholder="$t('bianjiziliao[11]')"></el-input>
            </div>
             <div class="zhanwei"></div>
        </div>
        <div class="item">
            <div></div>
            <div>
                <div class="btn" @click="setUser()">{{$t('bianjiziliao[12]')}}</div>
            </div>
             <div class="zhanwei"></div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            form:{
                name:'',//昵称
                real_name:'',//真实姓名
                sex:'',//性别
                phone:'',//手机号
                email:'',//邮箱
            }
        };
    },
    created:function(){
    },
    mounted:function(){
    },
    watch:{
    },
    methods:{
        //修改资料
        setUser(){
            var _this = this;
            if(!_this.form.name || !_this.form.real_name || !_this.form.sex || !_this.form.phone || !_this.form.email){
                _this.$message({
                    message: _this.$t('bianjiziliao[13]'),
                    type: 'warning'
                });
                return ;
            }
            if(_this.form.sex == '男' || _this.form.sex == 'man'){
                _this.form.sex = 1
            }
            if(_this.form.sex == '女' || _this.form.sex == 'girl'){
                _this.form.sex = 0
            }
            _this.form.lang = _this.apis.lang
            _this.apis.post(_this.apis.update_user_info,_this.form).then(function(res) {
                if(res.data.code == 0){
                    _this.$message({
                        message: res.data.msg,
                        type: 'success'
                    }); 
                }else{
                    _this.$message({
                        message: res.data.msg,
                        type: 'success'
                    }); 
                }
            })
        }
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.ziliao{
    height: 631px;
    border: 1px solid #CACACA;
    padding: 20px 40px;
    margin-bottom: 100px;
    .hr{
        width: 98%;
        height: 1px;
        margin-left: 2%;
        margin-top: 25px;
        margin-bottom: 25px;
        background: #DEDEDE;
    }
    .title{
        color: #383838;
        font-size: 13px;
        font-weight: bold;
    }
    .item{
        display: flex;
        align-items: center;
        font-weight: bold;
        margin: 20px 0;
        &>div:nth-child(1){
            color: #383838;
            font-size: 13px;
            width: 100px;
            padding-left: 20px;
        }
        &>div:nth-child(2){
            flex: 344px;
            height: 32px;
            color: #383838;
            font-size: 13px;
        }
        .zhanwei{
            flex: 1;
        }
    }
    .btn{
        width: 57px;
        height: 26px;
        margin: 0 auto;
        margin-top: 40px;
        text-align: center;
        line-height: 26px;
        background: #FFAD01;
        font-size: 13px;
        color: #fff;
        font-weight: bold;
        cursor:pointer;
    }
}
</style>


